<template>
	<div id="introduction">
		<div class="introduction-wrap ">
			<div class="head-img"></div>
			<p>{{ content1 }}</p>
			<p>{{ content2 }}</p>
		</div>
	</div>
</template>
<script>
	import 'animate.css'
	var introduction = '您好，我叫彭华奎，28岁,毕业于东华理工大学。我对前端很感兴趣，今后的职业规划是朝着高阶前端工程师发展，目前开发主要使用vue.js框架。 写过响应式页面，移动端页面，pc端页面。如：企业网站，校园数字化管理系统，erp管理系统，SPA等。'
	export default{
		name:'Introduction',
		data(){
			return {
				content1:introduction,
				content2:'不浮躁，喜欢前端，执行力，学习能力都棒棒哒！'
			}
		}
	}
</script>
<style scoped>
	p{	
		margin:0;
		padding:0;
		text-indent:2em;
		line-height:28px;
		margin-bottom:12px;
	}
	.introduction-wrap{
		position:absolute;
		top:0;
		right:0;
		bottom:0;
		left:0;
		margin:auto;
		width:870px;
		height:460px;
		text-align:left;
		color:#D2691E;
		padding:30px;
		background-color:rgba(255,255,255,.4)
	}
	.head-img{
		width:190px;
		height:190px;
		margin-left:310px;
		border-radius:50%;
		background-image:url(./../../assets/images/me3.jpg);
		background-size: contain;
		margin-bottom:20px;
	}
</style>


