<template>
	<div id="side">
		<div class="side-shadow"></div>
		<div class="side-box">
			<div class="side-cut">
				<div class="side-bin">
					<div class="side-search"></div>
					<ul class="side-nav">
						<li v-for="item in navList">
							<a href="javascript:;">
								<i class="iconfont" v-html="item.icon"></i>
								{{ item.name }}
							</a>
						</li>
					</ul>
					<div class="side-foot"></div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	let navList = [
			{'name':'精彩首页','icon':'&#xe64e;'},
			{'name':'关于我','icon':'&#xe62b;'},
			{'name':'技能展示','icon':'&#xe628;'},
			{'name':'个人经历','icon':'&#xe63b;'},
			{'name':'项目展示','icon':'&#xe611;'},
			{'name':'联系我','icon':'&#xe62a;'}
		]
	export default {
		name:'Side',
		data(){
			return {
				navList:navList
			}
		}
	}
</script>
<style scoped>
	ul,li{
		margin:0;
		padding:0;
	}
	li{
		list-style:none;
	}
	a{
		text-decoration: none;
	}
	#side{
		display:none;
	}
	.side-box{
		position:fixed;
		top:0;
		right:0;
		width:300px;
		height:100%;
		z-index:1000;
		background-color:#fff;
	} 
	/*.side-cut{
		position: relative;
		width:284px;
		float:right;
	}*/
	.side-search{
		height: 60px;
		border-bottom:1px solid #aaa;
		margin-bottom:20px;
	}
	.side-shadow{
		position: absolute;
		top:0;
		right:0;
		width:3999px;
		height:100%;
		background:rgba(0,0,0,.5);
		z-index:99;
	}
	.side-nav{
		text-align:left;
	}
	.side-nav li{
		cursor:pointer;
	}
	.side-nav li a{
		margin-left:20px;
		color:#000;
	}
	.side-nav li a i{
		margin-right:20px;
	}
	.side-nav li{
		position:relative;
		height:70px;
		line-height:70px;
		width:100%;
	}
	.side-nav li:after{
		content:'';
		display:block;
		width:0;
		height:100%;
		position:absolute;
		top:0;
		right:0;
		background-color:#00c0ff;
		transition:all .25s ease-in-out;
		z-index:-1;
	}
	.side-nav li:hover:after{
		width:100%;
		background-color:#00c0ff;
	}
	.side-nav li:hover a{
		color:#fff;
	}
</style>


