<template>
	<div id="topNav">
		<ul class="nav-list">
			<li class="active"><a href=""></a>首页</li>
			<li><a href=""></a>关于</li>
			<li><a href=""></a>技能</li>
			<li><a href=""></a>经历</li>
			<li><a href=""></a>案例</li>
			<li><a href=""></a>联系</li>
		</ul>
	</div>
</template>
<script>
	export default {
		name:'TopNav',
		data(){
			return {}
		}
	}
</script>
<style scoped>
	ul,li{
		margin:0;
		padding:0;
	}
	li{
		list-style:none;
	}
	#topNav{}
	.nav-list{
		position:absolute;
		top:0;
		right:72px;
		display:flex;
		width:500px;
		height:80px;
		/*border:1px solid #f00;  #00c0ff*/
		justify-content:space-around;
		align-items:center;
		z-index:999;
	}
	.nav-list li{
		width:80px;
		height:34px;
		line-height:34px;
		background-color:rgba(255,255,255,0);
		border-radius:18px;
		cursor:pointer;
	}
	.nav-list li:hover a{
		color:#00c0ff;
	}
	.nav-list li.active{
		background-color:#00c0ff;
	}
</style>


