<template>
	<div id="homeBanner">
		<div class="swiper-container banner-swiper">
			<div class="swiper-wrapper">
				<div class="swiper-slide pic1">
					<card
					class="ani" 
			      	swiper-animate-effect="fadeIn" 
			      	swiper-animate-duration="0.5s" 
			      	swiper-animate-delay="0.3s"
					></card>
				</div>
				<div class="swiper-slide pic2">
					<card></card>
				</div>
				<div class="swiper-slide pic3">
					<card></card>
				</div>
				<div class="swiper-slide pic4">
					<card></card>
				</div>
			</div>
			<!-- 分页器 -->
			<div class="swiper-pagination banner-pagination"></div>
			<!-- 前进后退按钮 -->
			<div class="swiper-button-prev banner-btn-prev"></div>
			<div class="swiper-button-next banner-btn-next"></div>
		</div>
	</div>
</template>
<script>
	//第三方插件引入
	import Swiper from 'swiper';
	import 'swiper/dist/css/swiper.css';
	import $ from 'jquery'
	import card from '@/components/page/Card'
	import 'animate.css'
	export default{
		name:'HomeBanner',
		data(){
			return {}
		},
		components:{
			card
		},
		mounted(){
			var bannerSwiper = new Swiper(".banner-swiper",{
				autoplay:true,
				loop:true,
				grabCursor:true,
				// effect:"fade",
				// effect:"cube",
				// effect:"coverflow",
				// effect:"flip",
				pagination:{
					el:'.banner-pagination',
					clickable:true,
					//自定义分页器
					type:'custom',
					renderCustom:function(swiper,current,total){
						var _html = '';
						for (let i = 1; i <= total; i++) {
							if(current === i){
								_html += '<li class="banner-customs active"></li>';
							}else{
								_html += '<li class="banner-customs"></li>';
							}
						}
						return _html;
					}
				},
				navigation:{
					nextEl:'.banner-btn-next',
					prevEl:'.banner-btn-prev'
				},
				on:{
					init: function(){
						swiperAnimateCache(this); //隐藏动画元素 
						swiperAnimate(this); //初始化完成开始动画
					}, 
						slideChangeTransitionEnd: function(){ 
						swiperAnimate(this); //每个slide切换结束时也运行当前slide动画
					} 
			    }
			})

			$(function($){
				$('.banner-pagination').on('click','li',function(){
					var index = $(this).index()+1;
	       			//切换到slide，速度为1秒
	       			bannerSwiper.slideTo(index, 500, false);
				})
			})
		}
	}
</script>
<style scoped>
	#homeBanner{
		width:100%;
		height:100%;
	}
	.banner-swiper{
		width:100%;
		height:100%;
	}
	.swiper-slide{
		background-repeat: no-repeat;
		background-position: center center;
		background-size:cover;
	}
	.pic1{
		background-image: url('./../../assets/images/bg7.jpg')
	}
	.pic2{
		background-image: url('./../../assets/images/me.jpg')
	}
	.pic3{
		background-image: url('./../../assets/images/bg6.jpg')
	}
	.pic4{
		background-image: url('./../../assets/images/bg4.jpg')
	}
	.banner-pagination{
		position:absolute;
		left:50%;
		bottom:90px;
		margin-left:-110px;
		width:220px;
		display:flex;
		justify-content:space-around;
		z-index:100;
	}
	.banner-btn-prev{
		left:100px;
	}
	.banner-btn-next{
		right:100px;
	}
</style>


