<template>
	<div id="skills">
		<div id="container"></div>
	</div>
</template>
<script>
	import echarts from 'echarts'
	export default{
		name:'Skills',
		data(){
			return {}
		},
		mounted(){
			var dom = document.getElementById("container");
			var myChart = echarts.init(dom);
			var app = {};
			var option = null;
			app.title = '环形图';

			option = {
			    tooltip: {
			        trigger: 'item',
			        formatter: "{a} <br/>{b}: {c} ({d}%)"
			    },
			    legend: {
			        orient: 'vertical',
			        x: 'right',
			        data:['javaScript','css3','html5','vue.js','java']
			    },
			    series: [
			        {
			            name:'skills',
			            type:'pie',
			            radius: ['50%', '70%'],
			            avoidLabelOverlap: false,
			            label: {
			                normal: {
			                    show: false,
			                    position: 'center'
			                },
			                emphasis: {
			                    show: true,
			                    textStyle: {
			                        fontSize: '30',
			                        fontWeight: 'bold',
			                        color:'#f00'
			                    }
			                }
			            },
			            labelLine: {
			                normal: {
			                    show: false
			                }
			            },
			            data:[
			                {value:1548, name:'javaScript'},
			                {value:310, name:'css3'},
			                {value:234, name:'html5'},
			                {value:135, name:'vue.js'},
			                {value:100, name:'java'}
			            ]
			        }
			    ]
			};
			;
			if (option && typeof option === "object") {
			    myChart.setOption(option, true);
			}
		}
	}
</script>
<style scoped>
	#skills{
		height:100%;
	}
	#container{
		position:absolute;
		top:0;
		right:0;
		bottom:0;
		left:0;
		margin:auto;
		width:700px;
		height:500px;
		background-color:rgba(255,255,255,.5);
	}
</style>


