<template>
	<div id="logo">
		<div class="bogo-box">
			<h1 class="logoImg">
				<!-- <img src="./../../assets/images/fll.png" width="60" height="60" alt="LOGO"> -->
				<div class="img">
					<img src="./../../assets/images/fll.png" width="60" height="60" alt="LOGO">
				</div>
			</h1>
			<p class="info">
				<span class="name">{{ name }}</span>
				<span class="msg">{{ msg }}</span>
			</p>
		</div>
	</div>
</template>
<script>
	export default {
		name:'Logo',
		data(){
			return {
				name:'PHK',
				msg:'resume'
			}
		}
	}
</script>
<style scoped>
	h1,p,img{
		margin:0;
		padding:0;
	}
	.bogo-box{
		position: fixed;
		/*width:200px;*/
		height:60px;
		padding-left:75px;
		top:16px;
		left:30px;
		border:1px solid #ddd;
		border-radius:6px;
		background:rgba(255,255,255,0.5);
		text-align:left;
		display:table;
		font-size:20px;
		z-index:20;
	}
	.logoImg{
		float:left;
		display:inline-block;
		margin-left:-60px;
	}
	.img{
		width:60px;
		height:60px;
		background-color:rgba(255,255,255,.5);
		border-radius:50%;
	}
	.info{
		/*line-height:60px;*/
		display:table-cell;
		vertical-align: middle;
		padding-left:16px;
		padding-right:20px;
	}
	.name{
		font-weight:600;
		color:#fff;
		display:inline-block;
		padding:3px 10px;
		background-color:#000;
		border-radius:2px;
	}
	.msg{
		font-weight:600;
	}
</style>


