<template>
	<div id="audio">
		<div class="audio-wrapper">
			<i v-if="isPlay" id="audio-play" class="iconfont">&#xe63d;</i>
			<i v-else id="audio-pause" class="iconfont">&#xe616;</i>
			<img class="audio-img" src="./../../../static/audio/me3.jpg" width="60" height="60" alt="audio">
			<audio class="audio-player" autoplay>
			  <source src="./../../../static/audio/victory.mp3" type="audio/mpeg">
				Your browser does not support the audio tag.
			</audio>
		</div>
	</div>
</template>
<script>
	import $ from 'jquery'
	export default {
		name:'Audio',
		data(){
			return {
				isPlay:true
			}
		},
		mounted(){
			let _self = this;
			$(function($){
				/*jquery对象转换成js对象*/
				let player = $('.audio-player')[0];
				let angle = 0;
				var t;

				//音乐播放背景图片旋转
				var rotateFn = function(){
					t = setInterval(function(){
						angle += 20;
							if(angle>=360){
								angle = 0;
							}
							$('.audio-img').css({
								"transform":'rotate(' + angle + 'deg)',
								"transform-origin":"center center"
							});
					},500)
				};
				if(_self.isPlay){
					rotateFn();
				}
				$('.audio-wrapper').on("click",function(){
					if(_self.isPlay){
						player.pause(); //暂停
						clearInterval(t); //清空定时器
						_self.isPlay=false;
					}else{
						player.play(); //播放

						//背景图片旋转
						rotateFn();
						_self.isPlay=true;
					}
				})
			})
		}
	}
</script>
<style scoped>
	.audio-wrapper{
		position:absolute;
		right:50px;
		bottom:50px;
		width:60px;
		height:60px;
		line-height:60px;
		/*border:1px solid #f00;*/
		border-radius:50%;
		z-index:99;
		cursor:pointer;
	}
	.audio-img{
		position:absolute;
		top:0;
		left:0;
		z-index:-2;
		border-radius:50%;
	}
	.audio-wrapper i{
		color:rgba(255,255,255,.7);
		font-size: 26px;
	}
</style>


