<template>
	<div id="home">
		<div id="home-swiper" class="swiper-container home">
			<div class="swiper-wrapper">
				<div class="swiper-slide slide1">
					<!-- <card></card> -->
					<homeBanner></homeBanner>
					<div class="nextbtn">
						<i class="iconfont">&#xe62c;</i>
					</div>
				</div>
				<div class="swiper-slide slide2">
					<introduction
						class="ani" 
				      	swiper-animate-effect="fadeIn" 
				      	swiper-animate-duration="0.5s" 
				      	swiper-animate-delay="0.3s">
			      	</introduction>
				</div>
				<div class="swiper-slide slide3">
					<skills></skills>
				</div>
				<div class="swiper-slide slide4">
					<experience></experience>
				</div>
				<div class="swiper-slide slide5">
					<cases></cases>
				</div>
				<div class="swiper-slide slide6">
					<contact></contact>
				</div>
			</div>
			<!-- 分页器 -->
			<div class="swiper-pagination site"></div>

			<!-- 前进后退按钮 -->
			<!-- <div class="swiper-button-prev"></div>
			<div class="swiper-button-next"></div> -->
			<!-- 滚动条 -->
			<!-- <div class="swiper-scollbar"></div> -->
		</div>
	</div>
</template>
<script>
	//第三方插件引入
	import Swiper from 'swiper'
	import 'swiper/dist/css/swiper.css'
	import $ from 'jquery'
	import 'animate.css'

	import homeBanner from '@/components/common/HomeBanner'

	//自定义组件引入
	import card from '@/components/page/Card'
	import introduction from '@/components/page/Introduction'
	import skills from '@/components/page/Skills'
	import experience from '@/components/page/Experience'
	import cases from '@/components/page/Case'
	import contact from '@/components/page/Contact'
	export default{
		name:'Home',
		data(){
			return {}
		},
		components:{
			card,
			introduction,
			skills,
			experience,
			cases,
			contact,
			homeBanner
		},
		mounted(){
			var siteSwiper = new Swiper('#home-swiper',{
				// height:window.innerHeight,
				autoplay:false,
				mousewheel:true,
				loop:false,
				direction:'vertical',
				longSwipes:true,
				pagination:{
					el:'.site',
					clickable:true,
					//自定义分页器，必须设置type类型
					type:'custom',
					on:{
						init: function(){
							swiperAnimateCache(this); //隐藏动画元素 
							swiperAnimate(this); //初始化完成开始动画
						}, 
							slideChangeTransitionEnd: function(){ 
							swiperAnimate(this); //每个slide切换结束时也运行当前slide动画
						} 
			    	},
					renderCustom:function(swiper,current,total){
						var _html = '';
			            var t = "";
			            for (var i = 1; i <= total; i++) {
			            	switch(i){
								case 1:
									t="首页";
									break;
								case 2:
									t="关于";
									break;
								case 3:
									t="技能";
									break;
								case 4:
									t="经历";
									break;
								case 5:
									t="案例";
									break;
								case 6:
									t="联系";
									break;
			                }			                
			                if (current == i) {
			                    _html += '<li class="swiper-pagination-customs active">' + t + '</li>';
			                }else{
			                    _html += '<li class="swiper-pagination-customs">' + t  + '</li>';
			                }
			            }
			            return _html;
					}
				}
			});
			$(function($){
				$('.site').on('click','li',function(){
					var index = $(this).index()+1;
	       			//切换到第一个slide，速度为1秒
	       			siteSwiper.slideTo(index-1, 500, false);
				});
				$('.side-nav').on('click','li',function(){
					var index = $(this).index()+1;
					siteSwiper.slideTo(index-1, 500, false);
					$("#side").fadeToggle("fast");
			        $(".side-open hr").eq(1).fadeToggle("fast");
			        $(".side-open hr").eq(2).fadeToggle("fast");
			        if(isOpen){
			          isOpen = false;
			          startSideAnimate();
			        }else{
			          isOpen = true;
			          endSideAnimate();
			        }
				});
				$('.nextbtn').on('click',function(){
					//切换到第二个slide
					siteSwiper.slideTo(1, 500, false);
				});
			})
		}
		

	}
</script>
<style scoped>
	#home{
		width:100%;
		height:100%;
	} 
	.swiper-container{
		height:100%;
	}
	.swiper-slide{
		background-repeat: no-repeat;
		background-position: center center;
		background-size:cover;
	}
	.slide1{
		/*background-image:url('../assets/images/bg7.jpg');*/
	}
	.slide1 #card .card-wrap{
		color:#f00;
	}
	.slide2{
		background-image:url('../assets/images/bg2.jpg');
	}
	.slide3{
		background-image:url('../assets/images/bg8.jpg');
	}
	.slide4{
		background-image:url('../assets/images/bg9.jpg');
	}
	.slide5{
		background-image:url('../assets/images/bg5.jpg');
	}
	.slide6{
		background-image:url('../assets/images/bg10.jpg');
	}

	/* 分页器 */
	.site{
		position:absolute;
		top:0;
		left:50%;
		width:500px;
		height:80px;
		display:flex;
		justify-content:space-around;
		align-items:center;
		z-index:99;
	}

	.nextbtn{
		position: absolute;
		bottom:20px;
		left:50%;
		margin-left:-24px;
		width:48px;
		height:48px;
		border-radius:50%;
		background-color:rgba(255,255,255,.3);
		z-index:100;
		text-align: center;
		line-height:48px;
		cursor:pointer;
	}
	.nextbtn:hover{
		background-color:#00c0ff;
	}
	.nextbtn i.iconfont{
		color:#fff;
	}
	
</style>


