<template>
	<div id="contact">
		<div class="container">
			<div class="right-top-line"></div>
			<div class="bottom-line"></div>
			<div class="content">
				<h2 class="title"><span class="col-title">{{title}}</span>FOR ME</h2>
				<p class="tag-list">
					<em :class="getClass(index)" v-for="(item,index) in tags">
						{{ item }}
					</em>
				</p>
				<div class="p-txt">
					<p v-for="item in contentList">{{ item }}</p>
				</div>
			</div>
			<ul class="contact-list">
				<li v-for="item in icons"><i class="iconfont" v-html="item"></i></li>
			</ul>
		</div>
	</div>
</template>
<script>
	let tags = ['灵感','代码','梦想','未来'];
	let contentList = ['学习是一个充实愉悦的过程','喜欢尝试，期待新鲜事物','前端即兴趣，兴趣即未来','行路有良友，便是捷径','期待有机会与您共事！'];
	let icons = ['&#xe691;','&#xe61a;','&#xe60f;','&#xe651;','&#xe87c;'];
	export default{
		name:'Contact',
		data(){
			return {
				title:"CONTACT",
				tags:tags,
				contentList:contentList,
				icons:icons,
			}
		},
		methods:{
			getClass(index){
				return "em-col-"+(index+1);
			}
		}
	}
</script>
<style scoped>
	h2,p,ul,li{
		margin:0;
		padding:0;
	}
	li{
		list-style:none;
	}
	em{
		font-style:normal;
	}
	#contact{
		height:100%;
		width:100%;
		background-color:rgba(127,127,127,.4);
	}
	.container{
		position:absolute;
		top:0;
		right:0;
		bottom:0;
		left:0;
		margin:auto;
		width:500px;
		height:462px;
	}
	.right-top-line{
		position:absolute;
		top:0;
		right:0;
		width:150px;
		height:100px;
		border-top:6px solid #ffd200;
		border-right:6px solid #ffd200;
	}
	.bottom-line{
		position:absolute;
		bottom:0;
		width:500px;
		height:40px;
		border:6px solid #ffd200;
		border-top:none;
	}
	.content{
		position:absolute;
		top:0;
		right:0;
		bottom:0;
		left:0;
		margin:auto;
		width:360px;
		height:360px;
		z-index:3;
		/*border:1px solid #aaa;*/
	}
	.title{
		font-size:38px;
		border-bottom:1px solid #ffd200;
		padding-bottom:8px;
		color:#fff;
	}
	.col-title{
		color:#ffd200;
	}
	.em-col-1{
		color:#e3723c;
	}
	.em-col-2{
		color:#84c800;
	}
	.em-col-3{
		color: #33ccff
	}
	.em-col-4{
		color:#9e966e;
	}
	.tag-list{
		text-align:left;
		margin-top:12px;
		margin-bottom:24px;
	}
	.tag-list em{
		font-size:24px;
		margin-left:28px;
	}
	.p-txt{
		font-size:20px;
		color:#ddd;
	}
	.p-txt p{
		margin-bottom:8px;
	}
	.contact-list{
		position:absolute;
		bottom:45px;
		height:40px;
		width:302px;
		margin-left:100px;
		line-height:40px;
		z-index:10;
	}
	.contact-list li{
		float:left;
		display:inline-block;
		width:36px;
		height:36px;
		line-height:36px;
		border-radius:50%;
		background-color:#333;
		margin-left:20px;
		cursor:pointer;
	}
	.contact-list li:last-of-type{
		margin-right:20px;
	}
	.contact-list li:hover{
		background-color:#4169E1;
	}
	.iconfont{
		font-size:18px;
		color:#ddd;
	}
</style>


